import { ADD_EMAIL, REMOVE_EMAIL, CHANGE_EMAIL } from './types';

export const addEmail = () => ({
  type: ADD_EMAIL,
  payload: '',
});

export const removeEmail = id => ({
  type: REMOVE_EMAIL,
  payload: id,
});

export const changeEmail = (id, value) => ({
  type: CHANGE_EMAIL,
  payload: { id, value }
});



