import companies from 'src/data/companies.json';
import {
  INFORMATION,
  POLICY,
  CUSTOM_INPUT_ID,
  SORT_INFORMATION,
  INFORMATION_CUSTOM,
  INFORMATION_CUSTOM_DELETE,
} from '../actions/types';

const initialState = {};

export function informReducer(state = initialState, action) {
  switch (action.type) {
    case INFORMATION:
      if (action.payload.name === 'companyId') {
        return {
          ...state,
          ...companies.find(item => item.id === action.payload.val).information,
          [action.payload.name]: action.payload.val,
        };
      }
      return { ...state, [action.payload.name]: action.payload.val };
    default:
      return state;
  }
}

const initialStateArr = [];

export function informReducerCustom(state = initialStateArr, action) {
  switch (action.type) {
    case INFORMATION_CUSTOM:
      return [
        ...state.filter(item => item.id !== action.payload.id),
        {
          id: action.payload.id,
          value: action.payload.val,
        },
      ];
    case INFORMATION_CUSTOM_DELETE:
      return [...state.filter(item => item.id !== action.payload)];
    default:
      return state;
  }
}

export function sortedIdsReducer(state = initialStateArr, action) {
  switch (action.type) {
    case SORT_INFORMATION:
      return action.payload;
    default:
      return state;
  }
}

const initialStateBool = false;

export function policyReducer(state = initialStateBool, action) {
  switch (action.type) {
    case POLICY:
      return action.payload;
    default:
      return state;
  }
}

const initialStateInputs = [];

export function inputsReducer(state = initialStateInputs, action) {
  switch (action.type) {
    case CUSTOM_INPUT_ID: {
      return action.payload;
    }
    default:
      return state;
  }
}



