import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Button from 'src/common/Button';
import SignTemplate from './SignTemplate/SignTemplate';
import CustomTemplate from './CustomTemplate/CustomTemplate';
import Clarification from './Сlarification/Clarification';
import MailPart from './MailPart/MailPart';

import styles from './Preview.css';

class Preview extends Component {
  static propTypes = {
    arrInputs: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    mainObj: PropTypes.shape().isRequired,
    isCustom: PropTypes.bool,
  };

  static defaultProps = {
    isCustom: false,
  };

  constructor() {
    super();

    this.previewRef = React.createRef();
  }

  handleClick = () => {
    const target = this.previewRef.current;
    let rng;
    let sel;
    if (document.createRange) {
      rng = document.createRange();
      rng.selectNode(target);
      sel = window.getSelection();
      sel.removeAllRanges();
      sel.addRange(rng);
      document.execCommand('copy');
    } else {
      rng = document.body.createTextRange();
      rng.moveToElementText(target);
      rng.select();
      document.execCommand('copy');
    }
  };

  render() {
    const { mainObj, isCustom, arrInputs } = this.props;

    return mainObj.companyId || arrInputs.length ? (
      <Fragment>
        <div className={styles.containerInner}>
          <MailPart />
          <div className={styles.templateWrapper} ref={this.previewRef}>
            {isCustom ? <CustomTemplate /> : <SignTemplate />}
          </div>
        </div>
        <div className={styles.buttonWrapper}>
          <Button
            className={styles.button}
            handleClick={this.handleClick}
            title="Copy Signature"
          />
        </div>
      </Fragment>
    ) : (
      <Clarification />
    );
  }
}

const mSTP = state => ({
  mainObj: state.mainObj,
  arrInputs: state.inputs,
});
export default connect(mSTP)(Preview);



