import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { setPolicy } from 'src/redux/actions/information';
import FormGroup from 'src/common/FormGroup';
import Checkbox from 'src/common/Checkbox';
import Contacts from './components/Contacts/Contacts';
import PersonalInfo from './components/PersonalInfo/PersonalInfo';
import Address from './components/Address/Address';

import styles from './CreateForm.css';

class CreateForm extends Component {
  static propTypes = {
    policy: PropTypes.bool.isRequired,
    onChangePolicy: PropTypes.func.isRequired,
  };

  render() {
    const { onChangePolicy, policy } = this.props;

    return (
      <div className={styles.container}>
        <div className={styles.toCustomWrapper}>
          <Link
            className={styles.toCustom}
            to={`${process.env.PUBLIC_URL}/custom`}>
            Go to custom mode
          </Link>
        </div>
        <FormGroup title="Personal info">
          <PersonalInfo />
        </FormGroup>
        <FormGroup title="Address">
          <Address />
        </FormGroup>
        <FormGroup title="Contacts">
          <Fragment>
            <Contacts />
            <Checkbox
              className={styles.policy}
              title="- Add security policy notice"
              handleClick={() => onChangePolicy(!policy)}
            />
          </Fragment>
        </FormGroup>
      </div>
    );
  }
}

const mSTP = state => ({
  policy: state.policy,
});

const mDTP = dispatch => ({
  onChangePolicy: bool => dispatch(setPolicy(bool)),
});

export default connect(
  mSTP,
  mDTP,
)(CreateForm);



