import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { arrayMove } from 'react-sortable-hoc';
import {
  setInformCustom,
  setCustomInputId,
  setSort,
  setCustomDelete,
} from 'src/redux/actions/information';
import FormGroup from 'src/common/FormGroup';
import Select from 'src/common/Select';
import fields from 'src/data/fields.json';
import SortableList from './SortableList/SortableList';

import styles from './CustomForm.css';

let arrIds = [];
let count = 0;
let valueType = null;
let valuePhone = null;
let itemsArr = [];

class CustomForm extends Component {
  static propTypes = {
    onChangeInputs: PropTypes.func.isRequired,
    arrInputs: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    onChange: PropTypes.func.isRequired,
    mainObj: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    onSorted: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
  };

  state = {
    value: null,
  };

  onSortEnd = ({ oldIndex, newIndex }) => {
    itemsArr = arrayMove(itemsArr, oldIndex, newIndex);
    this.props.onChangeInputs(itemsArr);
    const sortIds = itemsArr.map(item => item.id);
    this.props.onSorted(sortIds);
  };

  handleChange = e => {
    const { value } = e.target;

    let valueNumb = +value;
    count += 1;
    if (valueNumb === 7) {
      valueNumb = fields.length + count;
    }
    if (valueNumb === 8) {
      valueNumb = 11111 + count;
    }
    arrIds.push(valueNumb);
    itemsArr = [
      ...itemsArr,
      { ...fields.find(item => item.id === +value), id: valueNumb },
    ];
    const sortIds = itemsArr.map(item => item.id);
    this.props.onChangeInputs(itemsArr);
    this.props.onSorted(sortIds);
  };

  handleChangePhone = e => {
    const { value, name, id } = e.target;
    let phoneObj = {};
    if (name === 'phoneType') {
      valueType = value;
    }
    if (name === 'phone') {
      valuePhone = value;
    }
    phoneObj = {
      ...phoneObj,
      phone: valuePhone,
      phoneType: valueType,
    };
    this.props.onChange(name, phoneObj, +id);
  };

  handleDelete = id => {
    const sortIds = itemsArr.map(item => item.id);
    const arrSortIds = sortIds.filter(item => item !== id);
    itemsArr = [...itemsArr.filter(item => item.id !== id)];
    arrIds = arrIds.filter(item => item !== id);
    this.props.onChangeInputs(itemsArr);
    this.props.onSorted(arrSortIds);
    this.props.onDelete(id);
  };

  render() {
    const { onChange, arrInputs, mainObj } = this.props;

    const arrObjInputs = fields.map(item => {
      let id = 0;
      switch (item.id) {
        case fields.length + count:
          id = '';
          break;
        default:
          id = item.id;
      }
      return arrIds.includes(id) ? { ...item, disabled: true } : item;
    });

    return (
      <div className={styles.wrapper}>
        <div className={styles.toCustomWrapper}>
          <Link className={styles.toCustom} to={`${process.env.PUBLIC_URL}/`}>
            Back to main mode
          </Link>
        </div>
        <FormGroup title="Custom">
          <Fragment>
            <SortableList
              items={arrInputs}
              onChange={onChange}
              handleChangePhone={this.handleChangePhone}
              handleDelete={this.handleDelete}
              mainObj={mainObj}
              onSortEnd={this.onSortEnd}
            />
            <Select
              onChange={e => this.handleChange(e)}
              label="New Option"
              values={arrObjInputs}
              unselected
              name="select-custom"
              id="select-custom"
            />
          </Fragment>
        </FormGroup>
      </div>
    );
  }
}

const mSTP = state => ({
  arrInputs: state.inputs,
  sortedIds: state.sortedIds,
  mainObj: state.mainObjCustom,
});

const mDTP = dispatch => ({
  onChangeInputs: arr => dispatch(setCustomInputId(arr)),
  onChange: (name, val, id) => dispatch(setInformCustom(name, val, id)),
  onSorted: ids => dispatch(setSort(ids)),
  onDelete: id => dispatch(setCustomDelete(id)),
});

export default connect(
  mSTP,
  mDTP,
)(CustomForm);



