import React from 'react';
import styles from './MailPart.css';

const MailPart = () => (
  <div className={styles.wrapper}>
    <div className={styles.row}>To:</div>
    <div className={styles.row}>Cc:</div>
    <div className={styles.row}>Subject:</div>
  </div>
);

export default MailPart;



