import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Input from 'src/common/Input';
import { setInform } from 'src/redux/actions/information';

import styles from './Address.css';

function Adress({ onChange, mainObj }) {
  return (
    <Fragment>
      <Input
        handleInput={e => onChange(e.target.name, e.target.value)}
        name="street"
        label="Street"
        value={mainObj.street}
        reg={/^[a-z ,.'-]+$/i}
        type="text"
      />
      <div className={styles.details}>
        <Input
          handleInput={e => onChange(e.target.name, e.target.value)}
          name="city"
          label="City"
          className={styles.input_detailed}
          value={mainObj.city}
          reg={/^[a-z ,.'-]+$/i}
          type="text"
        />
        <Input
          handleInput={e => onChange(e.target.name, e.target.value)}
          name="zipcode"
          label="Zipcode"
          className={styles.input_detailed}
          value={mainObj.zipcode}
          reg={/(^\d{5}(?:[\s]?[-\s][\s]?\d{4})?$)/}
          type="text"
        />
        <Input
          handleInput={e => onChange(e.target.name, e.target.value)}
          name="country"
          label="Country"
          className={styles.input_detailed}
          value={mainObj.country}
          reg={/^[a-z ,.'-]+$/i}
          type="text"
        />
      </div>
    </Fragment>
  );
}

Adress.propTypes = {
  onChange: PropTypes.func.isRequired,
  mainObj: PropTypes.shape().isRequired,
};

const mSTP = state => ({
  mainObj: state.mainObj,
});

const mDTP = dispatch => ({
  onChange: (name, val) => dispatch(setInform(name, val)),
});

export default connect(
  mSTP,
  mDTP,
)(Adress);



