import React from 'react';
import { PropTypes } from 'prop-types';
import styles from 'src/assets/style.css';

const Select = ({
  values,
  label,
  className,
  onChange,
  name,
  unselected,
  id,
  value,
}) => (
  <div className={[styles.selectContainer, className].join(' ')}>
    <span className={styles.selectContainerTitle}>{label}</span>
    <select
      onChange={onChange}
      className={styles.select}
      name={name}
      id={id}
      value={value}>
      {unselected && (
        <option key={0} value={0} disabled>
          Choose {label}
        </option>
      )}
      {values &&
        values.map(item => (
          <option disabled={item.disabled} key={item.id} value={item.id}>
            {item.value}
          </option>
        ))}
    </select>
  </div>
);

Select.propTypes = {
  values: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number.isRequired,
      value: PropTypes.string.isRequired,
    }),
  ).isRequired,
  unselected: PropTypes.bool,
  className: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

Select.defaultProps = {
  unselected: false,
  className: '',
  label: '',
  value: 0,
};

export default Select;



