import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import CustomForm from 'src/components/CustomForm/CustomForm';
import Preview from 'src/components/Preview/Preview';
import detectWindowWidth from 'src/common/detectWindowWidth';

import styles from './Custom.css';

class Custom extends Component {
  static propTypes = {
    width: PropTypes.number,
  };

  static defaultProps = {
    width: 1680,
  };

  state = {
    index: 0,
  };

  handleChange = (event, value) => {
    this.setState({
      index: value,
    });
  };

  handleChangeIndex = index => {
    this.setState({
      index,
    });
  };

  render() {
    const { index } = this.state;
    const { width } = this.props;
    const isMobile = width <= 768;

    const tabsTemplate = (
      <Tabs
        classes={{
          root: styles.tabsWrapper,
          flexContainer: styles.tabsContainer,
        }}
        TabIndicatorProps={{
          className: styles.tabIndicator,
        }}
        value={index}
        fullWidth
        onChange={this.handleChange}>
        <Tab
          classes={{
            root: [styles.tab, styles.tabLeft].join(' '),
            label: styles.tabLabel,
            labelContainer: styles.tabLabelContainer,
          }}
          label=""
          focus="true"
        />
        <Tab
          classes={{
            root: [styles.tab, styles.tabRight].join(' '),
            label: styles.tabLabel,
            labelContainer: styles.tabLabelContainer,
          }}
          label=""
        />
      </Tabs>
    );

    if (isMobile) {
      return (
        <Fragment>
          {tabsTemplate}
          <SwipeableViews index={index} onChangeIndex={this.handleChangeIndex}>
            <div className={styles.homeLeft}>
              <CustomForm />
            </div>
            <div className={styles.homeRight}>
              <Preview isCustom />
            </div>
          </SwipeableViews>
        </Fragment>
      );
    }
    return (
      <Fragment>
        {tabsTemplate}
        <div className={styles.wrapper}>
          <div className={styles.homeLeft}>
            <CustomForm />
          </div>
          <div className={styles.homeRight}>
            <Preview isCustom />
          </div>
        </div>
      </Fragment>
    );
  }
}

export default detectWindowWidth(Custom);



