import React from 'react';

import imgStep1 from 'src/assets/img/explaine_step_1.png';
import imgStep2 from 'src/assets/img/explaine_step_2.png';
import imgStep3 from 'src/assets/img/explaine_step_3.png';
import video from 'src/assets/video/signature1.2.mp4';

import styles from './Clarification.css';

const Clarification = () => (
  <div className={styles.container}>
    <h2 className={styles.h2}>Чтобы начать, следуйте шагам указанным ниже:</h2>
    <div className={styles.cards}>
      <div className={styles.card}>
        <div className={styles.cardItem}>
          <img className={styles.cardImgFirst} src={imgStep1} alt="step_1" />
        </div>
        <p className={styles.cardDescription}>Заполнить форму</p>
      </div>
      <div className={styles.card}>
        <div className={styles.cardItem}>
          <img className={styles.cardImgSecond} src={imgStep2} alt="step_2" />
        </div>
        <p className={styles.cardDescription}>Нажать “Copy Signature”</p>
      </div>
      <div className={styles.card}>
        <div className={styles.cardItem}>
          <img className={styles.cardImgThird} src={imgStep3} alt="step_3" />
        </div>
        <p className={styles.cardDescription}>Вставить в почтовый клиент</p>
      </div>
    </div>
    <div className="video">
      <h2 className={styles.videoTitle}>
        Предлагаем просмотреть обучающее видео по созданию подписи
      </h2>
      <video className={styles.video} src={video} controls type="video/mp4" />
    </div>
  </div>
);

export default Clarification;



