import { combineReducers } from 'redux';
import {
  policyReducer,
  inputsReducer,
  informReducer,
  sortedIdsReducer,
  informReducerCustom,
} from './information';
import emailReducer from './emails';
import phoneReducer from './phones';

const rootReducer = combineReducers({
  mainObj: informReducer,
  mainObjCustom: informReducerCustom,
  phone: phoneReducer,
  emails: emailReducer,
  policy: policyReducer,
  inputs: inputsReducer,
  sortedIds: sortedIdsReducer,
});

export default rootReducer;



