import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Item, Span, Image, Box } from 'react-html-email-browser';
import { connect } from 'react-redux';

import companies from 'src/data/companies.json';
import phoneTypes from 'src/data/phoneTypes.json';

const rowStyles = {
  padding: '0 0 16px 0',
  display: 'table',
};

const positionStyle = {
  fontFamily: 'Helvetica',
  fontWeight: 'bolder',
  color: '#000000',
  textTransform: 'uppercase',
  paddingLeft: '2px',
};

const fullName = {
  position: 'relative',
  paddingBottom: '10px',
};

const imageStyle = {
  paddingBottom: '12px',
  boxSizing: 'content-box',
};

class CustomTemplate extends Component {
  static propTypes = {
    mainObj: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    sortedIds: PropTypes.arrayOf(PropTypes.number).isRequired,
  };

  getPhoneType = type => {
    const typePhone = phoneTypes.find(elem => elem.id === type);
    if (type) return typePhone;
    return {
      val: '',
    };
  };

  getCompany = id => {
    const company = companies.find(elem => elem.id === id);
    if (company) return company;
    return {
      logo: '',
      name: '',
    };
  };

  render() {
    const { mainObj, sortedIds } = this.props;
    const itemRender = item => {
      switch (typeof item.value) {
        case 'string':
          if (item.id === 2) {
            return (
              <Item key={item.id} style={{ ...rowStyles, ...positionStyle }}>
                <Span fontSize={10}>{item.value}</Span>
              </Item>
            );
          } else if (item.id === 1) {
            return (
              <Item key={item.id} style={{ ...rowStyles, ...fullName }}>
                <Span fontSize={18} data-full="fullname">
                  {item.value}
                </Span>
              </Item>
            );
          }
          return (
            <Item key={item.id} style={{ ...rowStyles }}>
              <Span fontSize={12}>{item.value}</Span>
            </Item>
          );
        case 'number':
          return (
            <Item key={item.id} style={{ ...imageStyle }}>
              <Image
                width={this.getCompany(item.value).logo.width}
                height={this.getCompany(item.value).logo.height}
                src={this.getCompany(item.value).logo.path}
                alt={this.getCompany(item.value).value}
              />
            </Item>
          );
        default:
          return (
            <Item key={item.id}>
              <Span fontSize={12} style={{ ...rowStyles }}>
                {this.getPhoneType(+item.value.phoneType).val}
                {item.value.phone}
              </Span>
            </Item>
          );
      }
    };
    return (
      <Box align="left">
        {sortedIds.map(id => {
          const myItem = mainObj.find(item => item.id === id);
          if (myItem) return itemRender(myItem);
          return null;
        })}
      </Box>
    );
  }
}

const mSTP = state => ({
  phone: state.phone,
  mainObj: state.mainObjCustom,
  emails: state.emails,
  policy: state.policy,
  sortedIds: state.sortedIds,
});

export default connect(mSTP)(CustomTemplate);



