import {
  INFORMATION,
  CUSTOM_INPUT_ID,
  POLICY,
  SORT_INFORMATION,
  INFORMATION_CUSTOM,
  INFORMATION_CUSTOM_DELETE,
} from './types';

export const setInform = (name, val) => ({
  type: INFORMATION,
  payload: { name, val },
});

export const setInformCustom = (name, val, id) => ({
  type: INFORMATION_CUSTOM,
  payload: { name, val, id },
});

export const setCustomDelete = id => ({
  type: INFORMATION_CUSTOM_DELETE,
  payload: id,
});

export const setPolicy = policy => ({
  type: POLICY,
  payload: policy,
});

export const setCustomInputId = arr => ({
  type: CUSTOM_INPUT_ID,
  payload: arr,
});

export const setSort = ids => ({
  type: SORT_INFORMATION,
  payload: ids,
});



