import {
  UPDATE_PHONE,
  ADD_PHONE,
  REMOVE_PHONE,
  PHONE_TYPE,
} from '../actions/types';

const initialStateObj = [{ id: 0, phone: '', type: 1 }];

export default function phoneReducer(state = initialStateObj, action) {
  switch (action.type) {
    case UPDATE_PHONE:
      return [
        ...state.map(
          item =>
            item.id === action.payload.id
              ? { id: item.id, phone: action.payload.phone, type: item.type }
              : item,
        ),
      ];

    case ADD_PHONE:
      return [
        ...state,
        { id: state[state.length - 1].id + 1, phone: '', type: 1 },
      ];

    case REMOVE_PHONE:
      return state.filter(item => item.id !== action.payload);

    case PHONE_TYPE: {
      return [
        ...state.map(
          item =>
            item.id === action.payload.id
              ? {
                  id: action.payload.id,
                  phone: item.phone,
                  type: action.payload.type,
                }
              : item,
        ),
      ];
    }
    default:
      return state;
  }
}



