import React, { Component } from 'react';
import { PropTypes } from 'prop-types';
import styles from 'src/assets/style.css';
import ValidationInput from 'src/common/Validity';

class Input extends Component {
  static propTypes = {
    label: PropTypes.string.isRequired,
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
    plus: PropTypes.bool.isRequired,
    increment: PropTypes.func.isRequired,
    decrement: PropTypes.func.isRequired,
    handleInput: PropTypes.func,
    name: PropTypes.string.isRequired,
    className: PropTypes.string,
    button: PropTypes.bool,
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    handleBlurInput: PropTypes.func,
    type: PropTypes.string.isRequired,
    reg: PropTypes.any.isRequired, // eslint-disable-line
  };

  state = {
    isActive: false,
    valueInput: '',
    isValid: true,
  };

  handleFocus = () => {
    this.setState({ isActive: true });
  };

  handleBlur = e => {
    this.props.handleBlurInput(e);
    this.setState({
      isActive: !this.state.isActive,
      valueInput: e.target.value,
    });
  };

  handleChange = e => {
    this.props.handleInput(e);
    this.setState({
      isValid: ValidationInput(e.target.value, this.props.reg),
    });
  };

  render() {
    const {
      label,
      id,
      plus,
      increment,
      decrement,
      name,
      className,
      button,
      value,
      type,
    } = this.props;

    const { valueInput, isValid } = this.state;
    return (
      <div
        className={[
          /* eslint-disable no-nested-ternary */
          !isValid
            ? styles.inputInValid
            : this.state.isActive
              ? styles.inputActive
              : styles.input,
          className,
        ].join(' ')}>
        <input
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
          onChange={this.handleChange}
          className={styles.inputField}
          id={id}
          type={type}
          name={name}
          value={value}
        />
        <label
          className={
            this.state.isActive || valueInput || value
              ? styles.inputLabelActive
              : styles.inputLabel
          }
          htmlFor={id}>
          {label}
        </label>
        {button && (
          <button
            id={id}
            onClick={plus ? increment : decrement}
            className={plus ? styles.plus : styles.minus}
          />
        )}
      </div>
    );
  }
}

Input.defaultProps = {
  plus: false,
  increment: () => console.log('warning'),
  name: 'default',
  decrement: () => console.log('warning'),
  handleBlurInput: () => console.log('warning'),
  handleInput: () => console.log('warning'),
  id: '',
  className: '',
  button: false,
  value: '',
};

export default Input;



