import { UPDATE_PHONE, ADD_PHONE, REMOVE_PHONE, PHONE_TYPE } from './types'

export const updatePhone = (count, phone) => ({
  type: UPDATE_PHONE,
  payload: { id: count, phone }
})

export const addPhone = () => ({
  type: ADD_PHONE,
})

export const removePhone = id => ({
  type: REMOVE_PHONE,
  payload: id
})

export const setPhoneType = (id, type) => ({
  type: PHONE_TYPE,
  payload: { id, type }
})


