import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Box, Item, Span, Image } from 'react-html-email-browser';
import { connect } from 'react-redux';

import companies from 'src/data/companies.json';
import phoneTypes from 'src/data/phoneTypes.json';
import SECURITY_POLICY from 'src/data/texts';

class SignTemplate extends Component {
  static propTypes = {
    emails: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    phone: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    mainObj: PropTypes.shape().isRequired,
    policy: PropTypes.bool.isRequired,
  };

  getCompany = id => {
    const company = companies.find(elem => elem.id === id);
    if (company) return company;
    return {
      logo: '',
      name: '',
    };
  };

  getPhoneType = type => {
    const typePhone = phoneTypes.find(elem => elem.id === type);
    if (typePhone) return typePhone;
    return {
      val: '',
    };
  };

  render() {
    const { emails, phone, mainObj, policy } = this.props;
    const company = this.getCompany(mainObj.companyId);

    const rowStyles = {
      padding: '0 0 0 16px',
      borderLeft: '3px solid #efefef',
    };

    const paddingRow = {
      paddingTop: '6px',
      paddingBottom: '6px',
    };

    const coloredBorder = {
      borderLeft: `3px solid ${company.color}`,
    };

    const firstName = {
      fontFamily: 'Helvetica',
      fontSize: '18px',
    };

    const position = {
      color: '#8f8f8f',
    };

    const positionRow = {
      paddingBottom: '6px',
    };

    const spanStyles = {
      fontFamily: 'Helvetica',
      fontSize: '12px',
    };

    const inscription = {
      paddingBottom: '50px',
    };

    return (
      <Box>
        <Item style={{ ...inscription }} align="left">
          <Span style={{ ...spanStyles }}>{mainObj.inscription}</Span>
        </Item>
        <Item style={{ paddingBottom: '15px' }} align="left">
          <Image
            width={company.logo.width}
            height={company.logo.height}
            src={company.logo.path}
            alt={company.value}
          />
        </Item>
        <Item
          style={{ ...rowStyles, ...coloredBorder, ...paddingRow }}
          align="left">
          <Span style={{ ...firstName }}>{mainObj.name}</Span>
        </Item>
        <Item
          style={{ ...rowStyles, ...coloredBorder, ...positionRow }}
          align="left">
          <Span style={{ ...spanStyles, ...position }}>{mainObj.position}</Span>
        </Item>
        <Item style={{ ...rowStyles, ...paddingRow }} align="left">
          <Span style={{ ...spanStyles }}>{mainObj.street}</Span>
          <br />
          <Span style={{ ...spanStyles }}>
            {mainObj.city} {mainObj.zipcode}, {mainObj.country}
          </Span>
        </Item>
        <Item style={{ ...rowStyles, ...paddingRow }} align="left">
          {phone[0].phone !== '' &&
            phone.map(item => (
              <Fragment key={item.id}>
                <Span style={{ ...spanStyles }}>
                  {this.getPhoneType(item.type).val}
                  {item.phone}
                </Span>
                <br />
              </Fragment>
            ))}
        </Item>
        <Item style={{ ...rowStyles, paddingTop: '6px' }} align="left">
          {emails.map(item => (
            <Fragment key={item.id}>
              <Span style={{ ...spanStyles }}>{item.value}</Span>
              <br />
            </Fragment>
          ))}
        </Item>
        <Item style={{ ...rowStyles }} align="left">
          <Span style={{ ...spanStyles }}>{mainObj.website}</Span>
        </Item>
        <Item style={{ paddingTop: '40px' }} align="left">
          {policy && (
            <Span
              style={{
                color: '#8a8a8a',
              }}
              width={200}
              fontSize={10}>
              {SECURITY_POLICY}
            </Span>
          )}
        </Item>
      </Box>
    );
  }
}

const mSTP = state => ({
  phone: state.phone,
  mainObj: state.mainObj,
  companyId: state.companyId,
  emails: state.emails,
  policy: state.policy,
});

export default connect(mSTP)(SignTemplate);



