import { ADD_EMAIL, REMOVE_EMAIL, CHANGE_EMAIL } from '../actions/types';

const initialState = [{ id: 1, val: '' }];

export default function emailReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_EMAIL:
      return [...state, { id: state[state.length - 1].id + 1, val: '' }];

    case REMOVE_EMAIL:
      return [...state.filter(item => item.id !== action.payload)];

    case CHANGE_EMAIL: {
      return [
        ...state.map(
          item => (item.id === action.payload.id ? action.payload : item),
        ),
      ];
    }

    default:
      return state;
  }
}



