import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { setInform } from 'src/redux/actions/information';
import Select from 'src/common/Select';
import Input from 'src/common/Input';
import companies from 'src/data/companies.json';

const PersonalInfo = ({ onChange, fullName, position, company }) => (
  <Fragment>
    <Select
      onChange={e => onChange(e.target.name, parseInt(e.target.value, 10))}
      label="Company"
      values={companies}
      name="companyId"
      unselected
      id="company"
      value={company}
    />
    <Input
      label="Full name"
      handleInput={e => onChange(e.target.name, e.target.value)}
      name="name"
      type="text"
      value={fullName}
      reg={/^[a-z ,.'-]+$/i}
    />
    <Input
      label="Position"
      handleInput={e => onChange(e.target.name, e.target.value)}
      name="position"
      type="text"
      value={position}
      reg={/^[a-z ,.'-]+$/i}
    />
  </Fragment>
);

PersonalInfo.propTypes = {
  onChange: PropTypes.func.isRequired,
  fullName: PropTypes.string,
  position: PropTypes.string,
  company: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

PersonalInfo.defaultProps = {
  fullName: '',
  position: '',
  company: 0,
};

const mSTP = state => ({
  fullName: state.mainObj.name,
  position: state.mainObj.position,
  company: state.mainObj.companyId,
});

const mDTP = dispatch => ({
  onChange: (name, val) => dispatch(setInform(name, val)),
});

export default connect(
  mSTP,
  mDTP,
)(PersonalInfo);



